import React from "react";
import style from "./Products.module.css";

const MiniProductCard = (props) => {
  let videoWidth = 450
  let videoHeight = Math.ceil(450/16*9)

  return (
      <div id={props.data.id} className={style.miniProductCard}>
        <iframe
          width={videoWidth}
          height={videoHeight}
          src={`https://www.youtube.com/embed/${props.data.videoUrl}`}
          frameborder="0"
          allowfullscreen
        ></iframe>

        <p>{props.data.name}</p>
      </div>
  );
};

export default MiniProductCard;
