import React from "react";
import { withRouter } from "react-router-dom";
import ProductCard from "./ProductCard";


class ProductCardContainer extends React.Component {
    render() {
        const newState = this.props.state.find((item) => item.id == this.props.match.params.userId);
        return(
        <ProductCard state={newState} userId={this.props.match.params.userId}/>
        )
    }
}

let ProductCardWithUrlData = withRouter(ProductCardContainer);

export default ProductCardWithUrlData;
