import React from 'react'
import "./App.css";
import state from "./state";
import FirstPage from "./components/FirstPage/FirstPage";
import { BrowserRouter, Route } from "react-router-dom";
import { Products } from "./components/Products/Products";
import { Nav } from "./components/Nav/Nav";
import { Logo } from "./components/Logo/Logo";
import About from "./components/About/About";
import Contacts from "./components/Contacts/Contacts";
import ProductCardWithUrlData from './components/ProductCard/ProductCardContainer';

function App() {
  return (
    <BrowserRouter>
      <div className={"App"}>
        <Logo />
        <Nav />
        <div className="empty"></div>
        <div className="content">
          <Route path="/main" render={() => <FirstPage />} />
          <Route path="/products" render={() => <Products  state={state} />} />
          <Route path="/about" render={() => <About />} />
          <Route path="/contacts" render={() => <Contacts />} />
          <Route path="/product/:typeOfItem/:userId" render={() => <ProductCardWithUrlData state={state.miniProductCard} />} />
        </div>
      </div>
    </BrowserRouter>
  );
}

export default App;
