import React from "react";
import MiniProductCard from "./MiniProductCard";
import style from "./Products.module.css";
import Sort from "./Sort";
import { BrowserRouter, Route } from "react-router-dom";
import {Tables, Nightstand, Dish, Servingboard, Cuttingboard} from "./ProductsByTypes/ProductsByTypes";
import ProductCard from '../ProductCard/ProductCardContainer'

const CardItems = (props) => {
  let cardItems = props.state.miniProductCard.map((data) => (
    <MiniProductCard data={data} />
  ));
  return <div className={style.container}>{cardItems}</div>;
};


export const Products = (props) => {
  let productsInfo = props.state.miniProductCard.map((data) => (
    data
  ));
  return (
    <BrowserRouter>
      <div className={style.parent}>
        <div className={style.div1}>
          {/* <Sort /> */}
        </div>
        <div className={`${style.div2}`}>
          <Route
            path="/products"
            exact
            render={() => <CardItems state={props.state} />}
          />
          <Route
            path="/products/table"
            exact
            render={() => <Tables state={props.state} />}
          />
          <Route
            path="/products/nightstand"
            exact
            render={() => <Nightstand state={props.state} />}
          />
          <Route
            path="/products/dish"
            exact
            render={() => <Dish state={props.state} />}
          />
          <Route
            path="/products/servingboard"
            exact
            render={() => <Servingboard state={props.state} />}
          />
          <Route
            path="/products/cuttingboard"
            exact
            render={() => <Cuttingboard state={props.state} />}
          />
        </div>
      </div>
    </BrowserRouter>
  );
};
